import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { BroadcastService, MsalService } from '@azure/msal-angular';
import { AlertsService } from '../app/services/alerts.service';
@Injectable({
  providedIn: 'root'
})
export class AuthService {
  requestObj = {
    appId: '120165c2-bd77-478e-86d3-36f9f74b8e11',
    scopes: [
      // 'user.read',
      // 'user.read.all',
      // 'securityevents.readwrite.all',
      // 'securityevents.read.all',
      // '.default',
      // 'SecurityEvents.ReadWrite.All',
      // 'SecurityEvents.Read.All',
      'profile',
      'offline_access',
      'user.read',
      'calendars.read'
    ],
  };
  accesstoken: any;
  accessTOken2: string;
  constructor(
    private authService: MsalService,
    private http: HttpClient,
    private alertService: AlertsService) { }
  async getAccessToken(): Promise<string> {
    // tslint:disable-next-line: prefer-const
    let result = await this.authService.acquireTokenSilent(this.requestObj)
      .catch((reason) => {
        console.log(reason);
      });

    if (result) {
      // Temporary to display token in an error box
      // this.alertsService.add('Token acquired', result.accessToken);
      console.log(result.idToken);
      this.accesstoken = result.accessToken;
      console.log(this.accesstoken);
      return result.accessToken;
    }
    return null;
  }

  getToken() {
    return this.accesstoken;
  }

  getAccessTokenFromBackend() {
    this.http.get<{message: string, accessToken: string}>('http://localhost:3001/api/token').subscribe(data => {
      console.log(data);
      console.log(data.accessToken);
      this.accessTOken2 = data.accessToken;
      localStorage.setItem('AccessTokenForAPI', data.accessToken);
      // this.alertService.getUserDetails();
    });
  }

  logout() {
    this.authService.logout();
  }

  getAccessToken2() {
    return this.accessTOken2;
  }

  getAlerts() {
    // this.http.get('http://localhost:3001/api/alert').subscribe(data => {
    //   console.log(data);
    // });
  }
}
