import { Component, OnInit, ViewChild } from '@angular/core';
import { MatDialog } from '@angular/material';
import { AlertsService } from '../../services/alerts.service';
import { MatTableDataSource, MatTableModule, MatSort, MatPaginator } from '@angular/material';

@Component({
  selector: 'app-dashboard-alerts',
  templateUrl: './dashboard-alerts.component.html',
  styleUrls: ['./dashboard-alerts.component.css']
})
export class DashboardAlertsComponent implements OnInit {

  alertData: MatTableDataSource<any>;
  alerts: any;
  displayedColumns: string[] = ['First Name', 'Last Name', 'Email', 'CollegeTest', 'dateTime', 'provider', 'manage'];
  @ViewChild(MatSort, {static: false}) sort: MatSort;
  @ViewChild(MatPaginator, {static: false}) paginator: MatPaginator;
  constructor(
    private dialog: MatDialog,
    private alertService: AlertsService) { }

  ngOnInit() {
    this.alertService.getAlerts();
    this.alertService.getUpdatedAlerts().subscribe(response => {
      console.log(response.result.value);
      this.alerts = response.result.value;
      this.alertData = new MatTableDataSource(this.alerts);
      this.alertData.sort = this.sort;
      this.alertData.paginator = this.paginator;
    });
  }
  // openAlerts(templateRef) {
  //   const dialogRef = this.dialog.open(templateRef, {
  //     width: '1200px',
  //     height: '650px'
  //     // data: { name: this.name, animal: this.animal }
  // });

  //   dialogRef.afterClosed().subscribe(result => {
  //     console.log('The dialog was closed');
  //     // this.animal = result;
  //   });
  // }

  applyFilter(filterValue: any) {
    this.alertData.filter = filterValue.trim().toLowerCase();
  }

  manageAlerts(alertsId, templateRef) {
    console.log(alertsId);
    const dialogRef = this.dialog.open(templateRef, {
          width: '500px',
          height: '400px'
          // data: { name: this.name, animal: this.animal }
      });

    dialogRef.afterClosed().subscribe(result => {
          console.log('The dialog was closed');
          // this.animal = result;
        });
  }
}
