import { BrowserModule } from '@angular/platform-browser';
import { BrowserAnimationsModule } from '@angular/platform-browser/animations';
import { NgModule } from '@angular/core';

import { MatToolbarModule, MatButtonModule, MatListModule, MatDialogModule, MatCardModule } from '@angular/material';

import { AppRoutingModule } from './app-routing.module';
import { AppComponent } from './app.component';
import { ProfileComponent } from './profile/profile.component';

import { AvatarModule } from 'ngx-avatar';

import { DashboardModule } from '../app/dashboard/dashboard.module';

import { MsalModule, MsalInterceptor } from '@azure/msal-angular';
import { HTTP_INTERCEPTORS, HttpClientModule } from '@angular/common/http';
import { AuthInterceptor } from './auth-interceptor';
import { AuthComponent } from './auth/auth.component';
import { NavBarComponent } from './nav-bar/nav-bar.component';

export const protectedResourceMaps: [string, string[]][] = [
  ['https://graph.microsoft.com/v1.0/me', ['user.read']],
  // ['https://graph.microsoft.com/v1.0/security/alerts', ['user.read.all', 'SecurityEvents.Read.All', 'SecurityEvents.ReadWrite.All']]
];

const isIE = window.navigator.userAgent.indexOf('MSIE ') > -1 || window.navigator.userAgent.indexOf('Trident/') > -1;

@NgModule({
  declarations: [
    AppComponent,
    ProfileComponent,
    AuthComponent,
    NavBarComponent
  ],
  imports: [
    DashboardModule,
    BrowserModule,
    AvatarModule,
    MatCardModule,
    AppRoutingModule,
    BrowserAnimationsModule,
    HttpClientModule,
    MatToolbarModule,
    MatButtonModule,
    MatDialogModule,
    MatListModule,
    AppRoutingModule,
    MsalModule.forRoot({
      auth: {
        clientId: '120165c2-bd77-478e-86d3-36f9f74b8e11',
        authority: 'https://login.microsoftonline.com/common',
        validateAuthority: true,
        redirectUri: 'http://localhost:3001/',
        postLogoutRedirectUri: 'http://localhost:3001/',
        navigateToLoginRequestUrl: true,
      },
      cache: {
        cacheLocation: 'localStorage',
        storeAuthStateInCookie: isIE, // set to true for IE 11
      },
    },
    {
      popUp: !isIE,
      consentScopes: [
        // '.default',
        // 'SecurityEvents.ReadWrite.All',
        // 'SecurityEvents.Read.All',
        // // 'user.read',
        // 'user.read.all',
        // 'securityevents.readwrite.all',
        // 'securityevents.read.all',
        // 'threatIndicators.ReadWrite.OwnedBy'
      'profile',
      'offline_access',
      'user.read',
      'calendars.read'
      ],
      unprotectedResources: ['https://www.microsoft.com/en-us/'],
      protectedResourceMap: protectedResourceMaps,
      extraQueryParameters: {}
    })
  ],
  providers: [
    // {
    //   provide: HTTP_INTERCEPTORS,
    //   useClass: MsalInterceptor,
    //   multi: true
    // },
    {
      provide: HTTP_INTERCEPTORS,
      useClass: AuthInterceptor,
      multi: true
    }
  ],
  bootstrap: [AppComponent]
})
export class AppModule { }
