import { Component, OnInit } from '@angular/core';
import { BroadcastService, MsalService } from '@azure/msal-angular';
import { Logger, CryptoUtils } from 'msal';
import { OAuthSettings } from '../oauthSettings';
import { Subject } from 'rxjs';
import { HttpClient } from '@angular/common/http';
import { AuthService } from '../auth.service';
import { MatDialog } from '@angular/material';

@Component({
  selector: 'app-nav-bar',
  templateUrl: './nav-bar.component.html',
  styleUrls: ['./nav-bar.component.css']
})
export class NavBarComponent implements OnInit {
  title = 'MSAL - Angular 8 Sample App';
  isIframe = false;
  loggedIn = false;
  users: any;

  constructor(
    private broadcastService: BroadcastService,
    private authService: MsalService,
    private http: HttpClient,
    private access: AuthService,
    private dialog: MatDialog) { }

  ngOnInit() {
    console.log('Hello From Navbar');
    this.isIframe = window !== window.parent && !window.opener;
    console.log('Hello From navbar2');
    this.checkoutAccount();

    this.broadcastService.subscribe('msal:loginSuccess', () => {
      this.checkoutAccount();
    });

    this.authService.handleRedirectCallback((authError, response) => {
      if (authError) {
        console.error('Redirect Error: ', authError.errorMessage);
        return;
      }

      console.log('Redirect Success: ', response);
      console.log('Access Token: ', response.accessToken);
      // this.access.getAccessTokenFromBackend();
      // const accessToken = this.getAccessToken().then(data => {
      //   console.log(data);
      // });
      // console.log(accessToken);
    });

    // const v: any = this.getAccessToken;
    // console.log(v);

    this.authService.setLogger(new Logger((logLevel, message, piiEnabled) => {
      console.log('MSAL Logging: ', message);
    }, {
      correlationId: CryptoUtils.createNewGuid(),
      piiLoggingEnabled: false
    }));


}



  checkoutAccount() {
    console.log(this.authService.getAccount());
    this.users = this.authService.getAccount();
    this.loggedIn = !!this.authService.getAccount();
    console.log(this.loggedIn);
    // if (localStorage.getItem('AccessTokenForAPI') !== null) {
    this.access.getAccessTokenFromBackend();
    // }
  }

  logout() {
    localStorage.removeItem('AccessTokenForAPI');
    this.authService.logout();
  }

  openModel(templateRef) {
    const dialogRef = this.dialog.open(templateRef, {
        width: '400px',
        height: '300px'
        // data: { name: this.name, animal: this.animal }
    });

    dialogRef.afterClosed().subscribe(result => {
        console.log('The dialog was closed');
        // this.animal = result;
    });
}
}
